<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> 3 товара </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <app-form-text
              title="ФИО"
              :error="formError.name"
              v-model="formData.name"
              placeholder="Введите ваше полное имя"
            >
            </app-form-text>

            <app-form-text
              title="Адрес доставки"
              :error="formError.address"
              v-model="formData.address"
              placeholder="Введите ваш адрес"
            >
            </app-form-text>

            <app-form-text
              title="Телефон"
              :error="formError.phone"
              v-model="formData.phone"
              placeholder="Введите ваш телефон"
            >
            </app-form-text>

            <app-form-text
              title="Email"
              :error="formError.email"
              v-model="formData.email"
              type="email"
              placeholder="Введи ваш Email"
            >
            </app-form-text>

            <app-form-textarea
              title="Комментарий к заказу"
              :error="formError.comment"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            >
            </app-form-textarea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{item.product.title}}</h3>
              <b>{{item.amount}} шт.- {{ item.amount * item.product.price }} ₽</b>
              <span>Артикул: {{item.product.id}}</span>
            </li>

          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{products.length}}</b> товара на сумму <b>{{totalPrice}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">Оформить заказ</button>
        </div>
        <div class="cart__error form__error-block" v-if="messageError">
          <h4>Заявка не отправлена!</h4>
          <p>Похоже произошла ошибка. {{messageError}}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { mapMutations, mapGetters } from 'vuex';
import AppFormText from '../components/AppFormText.vue';
import AppFormTextarea from '../components/AppFormTextarea.vue';
import { API_BASE_URL } from '@/config';

export default {

  components: { AppFormText, AppFormTextarea },
  data() {
    return {
      formData: {},
      formError: {},
      messageError: '',
    };
  },
  computed: {
    ...mapGetters({ products: 'carDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    ...mapMutations(['updateShowModal', 'updateMessageModal', 'resetCart', 'updateOrderInfo']),
    async order() {
      this.formError = {};
      this.messageError = '';
      try {
        const response = await axios.post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        });
        this.resetCart();
        this.formData = {};
        this.updateOrderInfo(response.data);
        this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
      } catch (e) {
        if (e.response.data.error.message) {
          this.messageError = e.response.data.error.message;
        } else {
          this.formError = e.response.data.error.request || {};
        }
      }
    },
  },
};
</script>
