import { createStore } from 'vuex';
import axiox from 'axios';
import numberFormat from '@/helpers/numberFormat';
import { API_BASE_URL } from '@/config';

export default createStore({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    colors: [],
    showModal: false,
    messageModal: '',
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const product = state.cartProducts.find((item) => item.productId === productId);
      if (product) {
        product.amount = amount;
      }
    },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, cartProductsData) {
      state.cartProductsData = cartProductsData;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
    loadColors(state, colorsData) {
      state.colors = colorsData;
    },
    updateShowModal(state, status) {
      state.showModal = status;
    },
    updateMessageModal(state, { message, request }) {
      if (message) {
        state.messageModal = message;
      } else {
        let mess = '';
        Object.keys(request).forEach((key) => {
          mess += (`Поле ${key} : ${request[key]}`);
        }, request);
        state.messageModal = mess;
      }
    },
  },
  getters: {
    orderInfo(state) {
      return state.orderInfo;
    },
    carDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return numberFormat(getters.carDetailProducts.reduce(
        (acc, item) => item.product.price * item.amount + acc, 0,
      ));
    },
    colors(state) {
      return state.colors;
    },
    showModal(state) {
      return state.showModal;
    },
    messageModal(state) {
      return state.messageModal;
    },
  },
  actions: {
    async loadOrderInfo(context, orderId) {
      try {
        const response = await axiox.get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
        context.commit('updateOrderInfo', response.data);
      } catch (e) {
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.message);
      }
    },
    async loadCart(context) {
      try {
        const response = await axiox.get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
        if (!context.state.userAccessKey) {
          localStorage.setItem('userAccessKey', response.data.user.accessKey);
          context.commit('updateUserAccessKey', response.data.user.accessKey);
        }
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch (e) {
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.message);
      }
    },
    async addProductToCart(context, { productId, amount }) {
      try {
        const response = await axiox.post(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch (e) {
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.message);
      }
    },
    async updateCartProductAmount(context, { productId, amount }) {
      if (amount < 1) {
        context.commit('updateCartProductAmount', { productId, amount: 1 });
      }
      context.commit('updateCartProductAmount', { productId, amount });
      try {
        const response = await axiox.put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
        context.commit('updateCartProductsData', response.data.items);
      } catch (e) {
        context.commit('syncCartProducts');
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.request.quantity);
      }
    },
    async loadColors(context) {
      try {
        const response = await axiox.get(`${API_BASE_URL}/api/colors`);
        context.commit('loadColors', response.data.items);
      } catch (e) {
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.message);
      }
    },
    async removeProduct(context, { productId }) {
      try {
        const response = await axiox.delete(`${API_BASE_URL}/api/baskets/products`, {
          data: {
            productId,
          },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      } catch (e) {
        context.commit('updateShowModal', true);
        context.commit('updateMessageModal', e.response.data.error.message);
      }
    },
  },
  modules: {
  },
});
