<template>
  <main class="content container" v-if="productLoading">Идет загрузка товара...</main>
  <main class="content container"
  v-if="productLoadingError">Произошла ошибка при загрузке товара</main>
  <main class="content container" v-if="productData">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="#"> {{category.title}} </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> {{product.title}} </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="product.image.file.url"
            :srcset="product.image2"
            :alt="product.title"
          />
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{product.id}}</span>
        <h2 class="item__title">{{product.title}}</h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price"> {{product.price}} ₽ </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="(color, index) in product.colors" :key="color.id">
                  <label class="colors__label">
                    <input
                      class="colors__radio sr-only"
                      type="radio"
                      name="color-item"
                      :value="color.code"
                      :checked="!index"
                    />
                    <span class="colors__value" :style="{'background-color': color.code}"> </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">

                <btn-control
                  ariaLabel="Убрать один товар"
                  typeBtn="minus"
                  @decrementAmount="decrementAmount"
                ></btn-control>

                  <input type="text" name="count" v-model.number="productAmount"/>

                <btn-control
                  ariaLabel="Добавить один товар"
                  typeBtn="plus"
                  @incrementAmount="productAmount++"
                ></btn-control>

              </div>

              <button
                class="button button--primery"
                type="submit"
                :disabled="productAddSending"
                >В корзину</button>
            </div>
            <div v-show="productAdded">Товар добавлен в корзину</div>
            <div v-show="productAddSending">Добавляем товар в корзину...</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"> Описание </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Характеристики </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Гарантия </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Оплата и доставка </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br />
            Синхронизация со смартфоном<br />
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br />
            Поддержка сторонних приложений<br />
          </p>

          <a href="#"> Все характеристики </a>

          <h3>Что это?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои
            велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS
            синхронизируется с датчиками по ANT+, объединяя полученную с них информацию. Данные
            отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное
            устройство можно установить как фирменное приложение, так и различные приложения
            сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал
            с целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные
            маршруты и планировать новые велопрогулки.
          </p>

          <h3>Дизайн</h3>

          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего
            74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета выполнен из
            черного пластика. На обращенной к пользователю стороне расположен дисплей диагональю 56
            мм. На дисплей выводятся координаты и скорость, а также полученная со смартфона и
            синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс
            и т.д. (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень
            защиты от влаги IPX7. Это означает, что устройство не боится пыли, а также выдерживает
            кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axiox from 'axios';
import { mapActions, mapMutations } from 'vuex';
import { API_BASE_URL } from '@/config';
import BtnControl from '../components/BtnControl.vue';

export default {
  components: { BtnControl },
  props: ['pageParams'],
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingError: false,

      productAdded: false,
      productAddSending: false,
    };
  },

  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },

  },
  methods: {
    ...mapActions(['addProductToCart']),
    ...mapMutations(['updateShowModal', 'updateMessageModal']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart(
        {
          productId: this.product.id,
          amount: this.productAmount,
        },
      ).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
    async loadProduct() {
      try {
        this.productLoading = true;
        this.productLoadingError = false;

        const response = await axiox.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`);
        this.productData = response.data;
        this.productLoading = false;
      } catch (e) {
        this.productLoadingError = true;
        this.productLoading = false;
        console.log('error', e);
      }
    },
    decrementAmount() {
      if (this.productAmount > 1) {
        this.productAmount -= 1;
      }
      return 1;
    },
  },
  created() {
    this.loadProduct();
  },
  beforeRouteUpdate() {
    this.loadProduct();
  },
  watch: {
    // '$route.params.id': {
    //   handler() {
    //     this.loadProduct();
    //   },
    //   immediate: true,
    // },
    productAmount() {
      if (this.productAmount < 1) {
        this.productAmount = 1;
        this.updateShowModal(true);
        this.updateMessageModal('Количество товара не может быть меньше 1');
      }
    },
  },
};
</script>

<style></style>
