<template>
   <button
        class="btnControl"
        type="button"
        :aria-label="ariaLabel"
        @click.prevent="typeBtn === 'minus' ? decrementAmount() : incrementAmount()"
        >
        <svg width="10" height="10" fill="currentColor">
          <use :xlink:href="`#icon-${typeBtn}`"></use>
        </svg>
      </button>
</template>

<script>
export default {
  props: ['ariaLabel', 'typeBtn'],
  methods: {
    decrementAmount() {
      this.$emit('decrementAmount');
    },

    incrementAmount() {
      this.$emit('incrementAmount');
    },
  },

};
</script>

<style lang="scss" scoped>
  .btnControl{
    cursor: pointer;
  }
</style>>
