<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info">
        {{productsData && productsData.pagination.total}} товар(а) </span>
    </div>

    <div class="content__catalog">
      <the-filter
        :price-from="filterPriceFrom"
        :price-to="filterPriceTo"
        :category-id="filterCategoryId"
        :filter-color="filterColor"
        @change-pricefrom="filterPriceFrom=$event"
        @change-priceto="filterPriceTo=$event"
        @change-category="filterCategoryId=$event"
        @change-color="filterColor=$event"
        @reset-page="page=1"
        ></the-filter>
      <section class="catalog">
        <app-loader v-if="productsLoading"></app-loader>
        <div v-if="productsLoadingError">Произошла ошибка при загрузке товара</div>
        <products-list :products="products"
        ></products-list>
        <the-pagination
          :page="page"
          @paginate="page = $event"
          :count="countProducts"
          :per-page="productsPerPage"
        ></the-pagination>
      </section>
    </div>
  </main>
</template>

<script>
import axiox from 'axios';
import TheFilter from '../components/TheFilter.vue';
import ProductsList from '../components/Products/ProductsList.vue';
import ThePagination from '../components/ThePagination.vue';
import AppLoader from '../components/AppLoader.vue';
import eventBus from '@/eventBus';
import { API_BASE_URL } from '@/config';

export default {
  name: 'Home',
  components: {
    TheFilter, ProductsList, ThePagination, AppLoader,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingError: false,
    };
  },
  computed: {

    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        })) : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {

    async loadProducts() {
      this.productsLoading = true;
      this.productsLoadingError = false;
      clearTimeout(this.loadProductsTimer);

      this.loadProductsTimer = setTimeout(async () => {
        try {
          const response = await axiox.get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColor,
            },
          });
          this.productsData = response.data;
          this.productsLoading = false;
        } catch (e) {
          this.productsLoadingError = true;
          this.productsLoading = false;
          console.log('error', e);
        }
      }, 0);
    },

  },

  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  created() {
    /* тут принимаем событие */
    eventBus.on('writePriceTolog', (price) => {
      console.log('price', price);
    });

    this.loadProducts();
  },
};
</script>
