<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        srcset="img/phone-square-3@2x.jpg 2x"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <div class="product__counter form__counter">
      <btn-control
        ariaLabel="Убрать один товар"
        typeBtn="minus"
        @decrementAmount="decrementAmount"
      ></btn-control>

      <input type="text" v-model.number="amount" name="count" />

      <btn-control
        ariaLabel="Добавить один товар"
        typeBtn="plus"
        @incrementAmount="amount++"
      ></btn-control>
    </div>

    <b class="product__price"> {{ item.amount * item.product.price }} ₽ </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>

import BtnControl from './BtnControl.vue';

export default {
  components: { BtnControl },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },

      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {

    deleteProduct(productId) {
      this.$emit('removeProduct', productId);
    },

    decrementAmount() {
      if (this.amount > 1) {
        this.amount -= 1;
      }
      return 1;
    },
  },
};
</script>

<style lang="scss" scoped>
  .product__del{
    cursor: pointer;
  }
</style>>
