<template>
  <router-link class="catalog__pic" :to="{name: 'product', params:{id: product.id}}" >
    <img :src="product.image" :srcset="product.image2" :alt="product.title" />
  </router-link>

  <h3 class="catalog__title">
     {{ product.title }}
  </h3>

  <span class="catalog__price" @click="writePriceTolog(product.price)">
     {{ formatedProducts.price }} ₽ </span>
  <ul class="colors colors--black">
    <li class="colors__item" v-for="color in product.colors" :key="color.id">
      <label class="colors__label" >
        <input class="colors__radio sr-only"
         type="radio" :value="color.id" v-model="currentColor" />
        <span class="colors__value" :style="{'background-color': color.code}"> </span>
      </label>
    </li>
  </ul>
</template>

<script>
import eventBus from '@/eventBus';
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      currentColor: this.product.colors[0].id,
    };
  },
  props: ['product'],
  methods: {
    writePriceTolog(price) {
      /* сделал ради ДЗ только, в vue3 пришлось использовать внешную библиотеку */
      eventBus.emit('writePriceTolog', price);
    },
  },
  computed: {
    /* так как нет в vue3 filters, по рекомендациям сделал через computed */
    formatedProducts() {
      return { price: numberFormat(this.product.price) };
    },
  },
};
</script>

<style></style>
